* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
	&:before, &:after {
		-webkit-box-sizing: border-box;
		-moz-box-sizing: border-box;
		box-sizing: border-box;
	}
}

*::selection {
	background-color: rgba(#cfe0ef, 0.8);
	color: #000;
}

a, a:active, a:hover {
	outline: none!important;
	text-decoration: none!important;
	color: $brand-primary;
	transition: 0.3s;
}


ul, p, h1, h2, h3, h4, h5, h6, .h1, .h2, .h3, .h4, .h5, .h6 {
	margin: 0;
	padding: 0;
}

ol, ul {
	list-style: none;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	font-family: 'MuseoSansCyrl', sans-serif;
	font-size: 16px;
	min-width: 320px;
	height: 100%;
	font-weight: 400;
	position: relative;
	line-height: 1.55;
	overflow-x: hidden;
	background-color: #000000;
	margin: 0;
	padding: 0;
}

.brand-color {
	color: $brand-primary;
}

.h1 {
	font-family: "MuseoSansCyrl";
	font-size: 38px;
	line-height: 1.25;
	font-weight: 900;
	color: #0e0e0f;
	@media(max-width: 767px) {
		font-size: 22px;
	}
}

.h2 {
	font-size: 30px;
	font-weight: 900;
	font-family: "MuseoSansCyrl";
	@media(max-width: 767px) {
		font-size: 20px;
	}
}

.h3 {
	font-size: 26px;
	font-weight: 700;
}

.h4 {
	color: #0e0f0f;
	font-size: 18px;
	font-weight: 600;
}


.text-center {
	text-align: center;
}

.text-right {
	text-align: right;
}

.text-left {
	text-align: left;
}

.text-uppercase {
	text-transform: uppercase;
}

.d-flex {
	display: -webkit-flex;
	display: -moz-flex;
	display: -ms-flex;
	display: -o-flex;
	display: flex;
}

.hidden {
	display: none;
}
.container{
	max-width: 1320px;
	width: 100%;
	margin: 0 auto;
	padding: 0px 15px;
	position: relative;
}
.btn_up.hide{
	@media(min-width: 767px) {
		display: block;
	}
}
.btn_down{
	cursor: pointer;
	img{
		width: 20px;
		height: 12px;
		position: absolute;
    left: 50%;
    bottom:20px;
    -webkit-transform: translate(-50%,-50%);
		-ms-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
		z-index:10;
		animation-name: scrollDown;
		animation-duration: 1.8s;
		animation-fill-mode: both;
		animation-iteration-count: infinite;
		animation-timing-function: ease-in;
	}
}
.btn_up{
	cursor: pointer;
	display: none;
	img{
	width: 20px;
	height: 12px;
	position: absolute;
  left: 50%;
  bottom:20px;	
	cursor: pointer;
	-webkit-transform: translate(-50%,-50%);
		-ms-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
    z-index: 99;
    animation-name: scrollDown;
		animation-duration: 1.8s;
		animation-fill-mode: both;
		animation-iteration-count: infinite;
		animation-timing-function: ease-in;
  }
}
@keyframes scrollDown {
	0% {
		bottom: 20px;
	}
	50% {
		bottom: 30px;
	}
	100% {
		bottom: 20px;
	}
}
