.title {
	.h1 {
		max-width: 400px;
		color: #ffffff;
		font-size: 36px;
		font-weight: 400;
		margin: 0 auto;
		@media(max-width: 767px) {
			max-width: 100%;
		}
	}
}	
.btn {
		a {
			max-width: 400px;
			margin:0 auto;
			padding-top:30px;
			display: block;
			color:#fff;
			@media(min-width: 767px) {
				&:hover{
					&:before {
						width: 60px;
					}
				}
			}
			&:before {
				content:' ';
				width: 40px;
				height: 2px;
				background-color: #ffffff;
				display: block;
				float: left;
				margin-right: 20px;
    		margin-top: 11px;
    		-webkit-transition: .3s;
    		-o-transition: .3s;
    		transition: .3s;
			}
			@media(max-width: 767px) {
				max-width: 100%;
			}
		}
	}

.animated_circle {
	width: 320px;
	height: 320px;
	margin:0 auto;
	position: relative;
	&:after {
		content:'';
		background: url(../../img/circle-bg.png) no-repeat;
		background-size: 100%;
		position: absolute;
		height: 134px;
    width: 510px;
    bottom: -25px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    @media(max-width: 550px) {
			width: 320px;
			height: 90px;
		}
	}
	@media(max-width: 550px) {
		width: 280px;
		height: 280px;
	}
}
.circle {
	&:before {
		content: '';
		width: 320px;
		height: 320px;
		border: 1px dashed #ffffff;
		opacity: 0.3;
		position: absolute;
    border-radius: 100%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
     @media(min-width: 767px){
    	-webkit-animation-name: circleAnimate;
	    animation-name: circleAnimate;
	    -webkit-animation-duration: 2s;
	    animation-duration: 2s;
	    -webkit-animation-direction: alternate;
	    animation-direction: alternate;
	    -webkit-animation-fill-mode: both;
	    animation-fill-mode: both;
	    -webkit-animation-iteration-count: 1;
	    animation-iteration-count: 1;
	    -webkit-animation-delay: 1s;
	    animation-delay: 1s;
	    -webkit-animation-timing-function: ease-in-out;
	    animation-timing-function: ease-in-out;
    }
    @media(max-width: 550px) {
			width: 280px;
			height: 280px;
		}
	}
}
.list_item {
	span{
		width: 29px;
		height: 29px;
		box-shadow: 0 3px 11px 3px rgba(0, 49, 43, 0.2);
		background-color: #648d88;
		border-radius:100%;
		display: block;
		position: absolute;
		top: 50%;
	  left: 50%;
	  -webkit-transform: translate(-50%,-50%);
	  -ms-transform: translate(-50%,-50%);
	  transform: translate(-50%,-50%);
	  -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
	}
	span:nth-child(2) {
			content: '';
			position: absolute;
			width: 44px;
			height: 44px;
			box-shadow: 0 3px 11px 3px rgba(0, 49, 43, 0.2);
			background-color: #648d88;
			opacity: 0.65;
			border-radius:100%;
			top: 50%;
		  left: 50%;
		  -webkit-transform: translate(-50%,-50%);
		  -ms-transform: translate(-50%,-50%);
		  transform: translate(-50%,-50%);
		  animation: spanAnim2;
			animation-duration: 1.4s;
			animation-direction: alternate;
			animation-fill-mode: both;
			animation-iteration-count: 1;
			animation-timing-function: ease-in-out;
		}
	img{
		position: absolute;
    z-index: 99;
	}
}
.circle {
	ul {
		height:215px;
		position: relative;
		.active{
			.hide_content {
			  opacity: 1;
			}
			span:nth-child(1){
				width: 38px;
				height: 38px
			}
		}
		li{
			.hide_content{
				opacity:0;
				-webkit-transition: opacity .1s ease-in;
		    -o-transition: opacity .1s ease-in;
		    transition: opacity .1s ease-in;
			}
  		img{
				top: 40%;
		    left: 50%;
		    -webkit-transform: translate(-50%,-50%);
		    -ms-transform: translate(-50%,-50%);
		    transform: translate(-50%,-50%);
			}
			&:hover{
				span:nth-child(1){
					width: 38px;
					height: 38px
				}
			.hide_content{
				opacity:1;
			}
			}
		}
		li:nth-child(1) {
			.list_item {
				position: absolute;
				bottom: 40px;
    		left: 5px;
			}
		}
		li:nth-child(2) {
			.list_item {
				position: absolute;
				bottom: 115px;
    		left: 15px;
			}
		}
		li:nth-child(3) {
			.list_item {
				position: absolute;
				top: 30px;
    		left: 60px;
			}
		}
		li:nth-child(4) {
			.list_item {
				position: absolute;
				position: absolute;
		    top: 0px;
		    left: 50%;
		    -webkit-transform: translate(-50%,-50%);
				-ms-transform: translate(-50%,-50%);
				transform: translate(-50%,-50%);
			}
		}
		li:nth-child(5) {
			.list_item {
				position: absolute;
				top: 30px;
    		right: 60px;
			}
		}
		li:nth-child(6) {
			.list_item {
				position: absolute;
				bottom: 115px;
    		right: 15px;
			}
		}
		li:nth-child(7) {
			.list_item {
				position: absolute;
				bottom: 40px;
    		right: 5px;
			}
		}
	}
}
.hide_content{
  position: absolute;
  left: 50%;
  bottom: -20px;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 250px;
  height: 150px;
  text-align: center;
  @media(max-width: 550px) {
		bottom: 5px;
	}
  img{
	 	height: auto;
	  width: auto;
	  max-height: 100px;
	  max-width: 150px;
	  position:absolute;
	  left: 50%;
	  top: 40%;
		-webkit-transform: translate(-50%,-50%);
		-ms-transform: translate(-50%,-50%);
		transform: translate(-50%,-50%);
  }
  p{
  	position: absolute;
  	bottom: 0px;
  	left: 0;
  	width: 100%;
  	color:#fff;
  	font-size:18px;
