	button{
		position: relative;
		background: none;
		border: none;
    font-size: 20px;
    font-weight: 700;
    display: inline-block;
    padding-left: 60px;
    -webkit-transition: .3s;
    -o-transition: .3s;
    transition: .3s;
    padding: 5px 0px 5px 60px;
    color: #fff;
    outline: none;
    cursor: pointer;
    @media(min-width: 767px) {
	    &:hover{
	    	padding-left: 80px;
	    	&:after{
	    		width: 70px;
	    	}
	    }
	  }
		&:after{
		content: '';
		background-color: transparent;
    height: 5px;
    width: 55px;
    position: absolute;
    top: 50%;
    left: 0px;
    background-image: url(../../img/wave-sm.svg);
    background-repeat: no-repeat;
    background-size: cover;
    -webkit-transform: translateY(-50%);
 		 -ms-transform: translateY(-50%);
  		transform: translateY(-50%);
		}
	}
	
}
input:focus::-webkit-input-placeholder {
    color: transparent;
} 
input::-moz-placeholder{ color: #fff; }
input::-webkit-input-placeholder { color: #fff; }

.form-control:focus + label {
	opacity: 1;
	top: -10px;
	left:-2px;
}
.popup{
	position: fixed;
	height: 100vh;
	width: 100%;
	top: 0px;
	left: 0px;
	z-index: 99;
	opacity: 0.95;
	justify-content: center;
	align-items: center;
	-webkit-transform: translateY(100%);
 		 -ms-transform: translateY(100%);
  		transform: translateY(100%);
  -webkit-transition: .3s;
    		-o-transition: .3s;
    		transition: .3s;
	.popup_logo{
		display: inline-block;
    width: 100%;
    padding-top: 40px;
		img{
			width: 110px;
	    margin: 0 auto;
	    display: block;
		}
	}
	.h1{
		text-align: center;
		max-width: 90%;
		padding-top: 60px;
		@media(max-width: 450px) {
			padding-top: 40px;
		}
	}
	.h3{
		text-align: center;
		max-width: 80%;
		padding-top: 10px;
	}
	.popup_content{
		position: relative;
		max-width: 870px;
		width: 90%;
		height: 530px;
		@media(max-width: 767px) {
			width: 100%;
			height: 100%;
			background-size: cover !important;
			overflow: auto;
			padding-bottom: 25px;
		}
	}
}
.popup.open_popup{
	-webkit-transform: translateY(0);
 		 -ms-transform: translateY(0);
  		transform: translateY(0);
}
.popup_form{
	max-width: 680px;
  margin: 0 auto;
  padding-top: 55px;
  width: 80%;
  @media(max-width: 950px) {
		padding-top: 40px;
	}
	@media(max-width: 450px) {
		padding-top: 20px;
	}
  .form_item:nth-child(2) {
		margin-right: 0px;
		margin-left: 15px;
		@media(max-width: 950px) {
			margin-left:0px;
			margin-top: 20px;
		}
  }
  .form_item {
    width: calc(33.33% - 15px);
    width: -moz-calc(33.33% - 15px);
		width: -webkit-calc(33.33% - 15px);
		width: calc(33.33% - 15px);	
    @media(max-width: 950px) {
			width: -moz-calc(50% - 15px);
			width: -webkit-calc(50% - 15px);
			width: calc(50% - 15px);	
		}
		@media(max-width: 950px) {
			width: 100%;
			margin-right:0px;
		}
	}
	.form_btn{
		width: 33.33%;
		padding-top: 14px;
		@media(max-width: 950px) {
			width: 100%;
			padding-top: 40px;
		}
		button {
	    font-size: 17px;
	    float: right;
		}
	}
}
.close{
	width: 24px;
	height: 24px;
	position: absolute;
	top: 20px;
  right: 20px;
	&:before{
		content: '';
  	background-image: url(../../img/close.png);
    width: 24px;
		height: 24px;
    position: absolute;
    bottom: 0px;
    left: 0px;
	}
}
.fadeInUp {
	@media(min-width: 768px){
		animation-name: fadeInUp;
		animation-duration: 0.8s;
		animation-iteration-count: 1;
		animation-fill-mode: both;
		animation-timing-function: linear;
	}
}
.animated1, .animated2, .animated3, .animated4, .animated5, .animated6, .sec2-anim, .sec3-anim {
    -webkit-animation-duration: .8s;
    animation-duration: .8s;
    -webkit-animation-iteration-count: 1;
    animation-iteration-count: 1;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
    -webkit-animation-timing-function: linear;
    animation-timing-function: linear;
}
.deley2 {
	animation-delay: 0.8s;
}
.sec2-anim,
.sec3-anim{
	-webkit-animation-delay: 2.2s;
    animation-delay: 2.2s;
}
.fixedImgAnim  {
	animation-name: fixedImgAnim ;
}
.animated1, .animated4 {
    -webkit-animation-delay: .4s;
    animation-delay: .4s;
}
.animated2, .animated5 {
    -webkit-animation-delay: 1.4s;
    animation-delay: 1.4s;
}
.animated3, .animated6 {
    -webkit-animation-delay: 2.8s;
    animation-delay: 2.4s;
}
@keyframes fadeInUp {
	0% {
		transform: translateY(40%);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes circleAnimate {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@keyframes spanAnim2 {
	0% {
		transform: translate(-50%, -50%) scale(0.6);
		opacity: 0.1;
	}
	100% {
		transform: translate(-50%, -50%) scale(1);
		opacity: 0.65;
	}
}
@keyframes fixedImgAnim {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

.clearfix:after, .clearfix:before {
  content: "";
  display: table;
  clear: both;
}

@import 'footer';
